body
  overflow: hidden
  
.container
  max-width: 600px
  border: 1px solid grey
  margin: 40px
  padding: 20px

  h2
    margin: 0
    margin-bottom: 10px
  
  p
    margin: 0

canvas
  margin: 40px
  position: absolute
  top: 400px
  left: 8px

img
  position: absolute
  right: 0
  top: 50%
  max-height: 80vh
  cursor: pointer
  transform: scale(1) translateY(-50%)
  transition: all .2s ease
  &:hover
    transform: scale(1.05) translateY(-50%)